import React, { useState } from "react";
import { Offcanvas, OffcanvasHeader, OffcanvasBody } from "reactstrap";
import { Controller, useForm } from "react-hook-form";
import { TextField, Button, Checkbox } from "@mui/material";
import baseurl from "../../util/axios";
import Upload from "../../components/upload";
interface modaltype {
  isOpen: boolean;
  toggole: () => void;
}
interface dataType {
  password: string;
  email: string;
  username: string;
  isAdmin: boolean;
  points: number;
  isInfint: boolean;
  text: string;
}
const INIT = {
  password: "",
  email: "",
  username: "",
  isAdmin: false,
  isInfint: false,
  points: 0,
  text: "",
};
const Modal = ({ isOpen, toggole }: modaltype) => {
  const { handleSubmit, control } = useForm({ defaultValues: INIT });
  const [open, setOpen] = useState<boolean>(false);
  const [file, setFile] = useState<any>([]);
  const submit = async (data: dataType) => {
    if (file.length > 0) {
      const formdata = new FormData();
      formdata.append("image", file[0]);
      Object.keys(data).forEach((a) => {
        if (a !== "id") {
          formdata.append(a, data[a as keyof typeof data] as string);
        }
      });
      await baseurl.post("/user/create", formdata);
    } else {
      await baseurl.post("/user/create", data);
    }
    window.location.reload();
  };
  return (
    <Offcanvas direction="end" isOpen={isOpen} toggle={toggole}>
      <OffcanvasHeader toggle={toggole}>new user</OffcanvasHeader>
      <OffcanvasBody>
        <form onSubmit={handleSubmit(submit)} className="form">
          <div className="input">
            <Controller
              control={control}
              name="username"
              render={({ field }) => (
                <TextField {...field} label="name" variant="outlined" />
              )}
            />
            <Controller
              control={control}
              name="email"
              render={({ field }) => (
                <TextField
                  {...field}
                  label="username"
                  variant="outlined"
                  type={"text"}
                />
              )}
            />
            <Controller
              control={control}
              name="text"
              render={({ field }) => (
                <TextField
                  {...field}
                  label="label"
                  variant="outlined"
                  type={"text"}
                />
              )}
            />
            <Controller
              control={control}
              name="points"
              render={({ field }) => (
                <TextField
                  {...field}
                  label="points"
                  variant="outlined"
                  type="number"
                />
              )}
            />
            <Controller
              control={control}
              name="password"
              render={({ field }) => (
                <TextField
                  {...field}
                  label="password"
                  variant="outlined"
                  type="password"
                />
              )}
            />

            <div>
              <label htmlFor="isAdmin">is Infint </label>
              <Controller
                control={control}
                name="isInfint"
                render={({ field: { name, onBlur, onChange, ref, value } }) => (
                  <Checkbox
                    id="isInfint"
                    name={name}
                    onBlur={onBlur}
                    onChange={onChange}
                    ref={ref}
                    checked={value}
                  />
                )}
              />
            </div>
            <Button type="submit" variant="outlined">
              save
            </Button>
          </div>
        </form>
      </OffcanvasBody>
      <Upload
        files={file}
        setFiles={setFile}
        open={open}
        toggole={() => setOpen(!open)}
      />
    </Offcanvas>
  );
};

export default Modal;
